import { tooltip } from "../Bindings/Chakra.fs.js";
import { createElement } from "react";
import { JS_newObj } from "../Bindings/JS.fs.js";

export function Tooltip(input) {
    return tooltip()((x) => {
        Object.assign(x, input.Props);
        x.backgroundColor = "gray.77";
        x.color = "black";
        x.zIndex = 20000;
    })(input.Props.children);
}

export function wrap(label, children) {
    return createElement(Tooltip, {
        Props: JS_newObj((x) => {
            let copyOfStruct = x;
            copyOfStruct.label = label;
            let copyOfStruct_1 = x;
            copyOfStruct_1.children = children;
        }),
    });
}

